const matchAttributesFromFaceAttributeInfos = (faceAttributeInfos) => {
  const attributes = {};

  //
  switch (1) {
    default:
      attributes.Background = [''];
  }

  //
  switch (faceAttributeInfos.Shape.Type) {
    case 0:
      attributes.Base = [''];
      break;
    // case 1:
    //   attributes.Base = [''];
    //   break;
    case 2:
      attributes.Base = [''];
      break;
    case 3 || 1:
      attributes.Base = [''];
      break;
    case 4:
      attributes.Base = [''];
      break;
    default:
      attributes.Base = [''];
  }

  //
  switch (faceAttributeInfos.Hair.Bang.Type) {
    case 0:
      attributes.Bangs = [''];
      break;
    case 1:
      attributes.Bangs = [''];
      break;
    default:
      attributes.Bangs = [''];
  }

  //
  switch (1) {
    default:
      attributes.Ear = [''];
  }

  //
  switch (1) {
    default:
      attributes.EarRing = [''];
  }

  //
  switch (faceAttributeInfos.Eyebrow.EyebrowLength.Type) {
    case 0:
      attributes.Eyebrows = [''];
      break;
    case 1:
      attributes.Eyebrows = [''];
      break;
    default:
      attributes.Eyebrows = [''];
  }
  switch (faceAttributeInfos.Eyebrow.EyebrowDensity.Type) {
    case 0:
      attributes.Eyebrows.push('');
      break;
    case 1:
      attributes.Eyebrows.push('');
      break;
    default:
      attributes.Eyebrows.push('');
  }
  switch (faceAttributeInfos.Eyebrow.EyebrowDensity.Type) {
    case 0:
      attributes.Eyebrows.push('');
      break;
    case 1:
      attributes.Eyebrows.push('');
      break;
    default:
      attributes.Eyebrows.push('');
  }

  // 
  switch (faceAttributeInfos.EyeOpen) {
    case false:
      attributes.Eyes = [''];
      break;
    case true:
      attributes.Eyes = [''];
      break;
    default:
      attributes.Eyes = [''];
  }
  switch (faceAttributeInfos.EyeSize) {
    case 0:
      attributes.Eyes.push('');
      break;
    // case 1:
    //   attributes.Eyes.push('');
    //   break;
    case 1 || 2:
      attributes.Eyes.push('');
      break;
    default:
      attributes.Eyes.push('');
  }

  //
  switch (faceAttributeInfos.Moustache.Type) {
    case 0:
      attributes.FacialHair = [''];
      break;
    case 1:
      attributes.FacialHair = [''];
      break;
    default:
      attributes.FacialHair = [''];
  }

  //
  // switch (faceAttributeInfos.Eye.Glass.Type) {
  //   case 0:
  //     attributes.Glasses = [''];
  //     break;
  //   case 1:
  //     attributes.Glasses = [''];
  //     break;
  //   case 2:
  //     attributes.Glasses = [''];
  //     break;
  //   default:
  //     attributes.Glasses = [''];
  // }

  switch (faceAttributeInfos.my_glass_type) {
    case 'no_glass':
      attributes.Glasses = [''];
      break;
    case 'round':
      attributes.Glasses = [''];
      break;
    case 'square':
      attributes.Glasses = [''];
      break;
    default:
      attributes.Glasses = [''];
  }

  //
  switch (faceAttributeInfos.Hair.Length.Type) {
    case 0:
      attributes.Hair = [''];
      break;
    case 1:
      attributes.Hair = [''];
      break;
    case 2:
      attributes.Hair = [''];
      break;
    case 3:
      attributes.Hair = [''];
      break;
    case 4:
      attributes.Hair = [''];
      break;
    default:
      attributes.Hair = [''];
  }

  //
  switch (faceAttributeInfos.Hat.Style) {
    case 0:
      attributes.Hat = [''];
      break;
    case 1 || 2 || 3:
      attributes.Hat = [''];
      break;
    default:
      attributes.Hat = [''];
  }

  //
  switch (1) {
    default:
      attributes.Headwear = [''];
  }

  //
  switch (faceAttributeInfos.Mask.Type) {
    case 1 || 3 || 4:
      attributes.Mask = [''];
      break;
    case 0 || 2:
      attributes.Mask = [''];
      break;
    default:
      attributes.Mask = [''];
  }

  //
  switch (faceAttributeInfos.Mouth.MouthOpen.Type) {
    case 0:
      attributes.Mouth = [''];
      break;
    case 1:
      attributes.Mouth = [''];
      break;
    default:
      attributes.Mouth = [''];
  }

  //
  switch (faceAttributeInfos.Nose.Type) {
    case 0:
      attributes.Nose = [''];
      break;
    case 1:
      attributes.Nose = [''];
      break;
    case 2:
      attributes.Nose = [''];
      break;
    case 3:
      attributes.Nose = [''];
      break;
    default:
      attributes.Nose = [''];
  }

  //
  switch (1) {
    default:
      attributes.Shirt = [''];
  }

  return attributes;
};

export default matchAttributesFromFaceAttributeInfos;
